import React from 'react';
import Axios from 'axios';
import Loader from './Loader'

class Contact extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            CompanyName: "",
            Email: "",
            ContactNo: "",
            Message: "",
            loading: false,
            buttonText: "Send Message"
        }
    }
    
    handleSubmit = (event) => {
        event.preventDefault();
        let {CompanyName, Email, ContactNo, Message} = this.state
        if(CompanyName.trim() != '' && Email.trim() != '' && ContactNo.trim() != ''){
            this.setState({
                loading: true
            })
            let url = "https://script.google.com/macros/s/AKfycbypbpqUTwr6EaUvN8p5Syjut815zrOIPMlpoA5dleh79T1UTPMW/exec?"
            url = url + "CompanyName=" + encodeURIComponent(CompanyName) + "&";
            url = url + "Email=" + encodeURIComponent(Email) + "&";
            url = url + "ContactNo=" + encodeURIComponent(ContactNo) + "&";
            url = url + "Message=" + encodeURIComponent(Message);
            Axios({
                url: url,
                method: 'GET'
            })
            .then((response)=>{
                if(response.status == 200){
                    this.setState({
                        loading: false
                    })
                    document.getElementById("contact-form").reset();
                    alert("Message sent. You will be contacted shortly.");
                    this.resetForm();
                }
            })
        }else{
            console.log("not send")
        }
    }

    resetForm = () => {
        this.state.CompanyName = "";
        this.state.Email = "";
        this.state.ContactNo = "";
    }

    handleChange = (event) => {
        this.setState({
            [event.target.name]:event.target.value
        })
    }

    render(){
        return(
            <div className="container ">
                <div className="content-section">
                <div className="contact-page-header">CONTACT US</div>
                
                <div className="row">
                    <div className="col-12 col-md-4">
                        <div Style="margin-bottom:40px; margin-top:18px">
                            <h6 className="info-header">Phone:</h6>
                            <p className="info-text">+91 98830 54909 / +91 79801 37084</p>
                        </div>
                        <div Style="margin-bottom:40px">
                            <h6 className="info-header">Email:</h6>
                            <p className="info-text">order@thokh.com</p>
                        </div>
                        <div Style="margin-bottom:40px">
                            <h6 className="info-header">Address:</h6>
                            <p className="info-text">Dashadrone, Check-Post Bangalaxmi Abasan</p>
                            <p className="info-text">Kolkata - 700136</p>
                        </div>
                    </div>
                    <div className="col-12 col-md-8">
                        <form className="form-style" id="contact-form" onSubmit={this.handleSubmit}> 
                            <input type="text" name="CompanyName" placeholder="Company Name*" className="input-style" onChange={this.handleChange} required /> <br/>
                            <input type="email" name="Email" placeholder="Email*" className="input-style" onChange={this.handleChange} required /><br/>
                            <input type="tel" name="ContactNo" placeholder="Contact No.*" className="input-style" onChange={this.handleChange}  pattern="[0-9]{10}"  required/><br/>
                            <textarea name="Message" placeholder="Message" className="input-style input-style-textarea" onChange={this.handleChange} /><br/>
                            <div className="contact-button-div">
                            <button type="submit" className="button-style contact-button-style" >
                                {this.state.loading ? <div Style="text-align:center"><Loader /></div>
                                    : this.state.buttonText}
                                </button>
                            </div>  
                        </form>
                    </div>
                </div>
                
                </div>
                
            </div>
        );
    }
    
}



export default Contact;