import React from 'react';
import Carousel from 'react-bootstrap/Carousel'
import Image1 from './assets/cimage1.jpeg'
import Image2 from './assets/cimage2.jpeg'
import Image3 from './assets/cimage3.jpeg'
const CarouselPage = () => {
    let imgs = [];
    imgs.push(Image1)
    imgs.push(Image2)
    imgs.push(Image3)
    let len = imgs.length
    return (
        <Carousel>
            {imgs.map((x)=> (<Carousel.Item>
                <img
                    className="d-block w-100"
                    src={x}
                    alt="First slide"
                />
            </Carousel.Item>))}
        </Carousel>
    )
}

export default CarouselPage;