import React from 'react';
import { Link } from 'react-router-dom';
import logo from './assets/logo.jpeg'
class Header extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            scrolledDown: false
        }
    }
    componentDidMount() {
        window.addEventListener('scroll', this.listenScrollEvent)
    }

    listenScrollEvent = e => {
        if (window.scrollY > 100) {
            this.setState({ scrolledDown: true })
        } else {
            this.setState({ scrolledDown: false })
        }
    }

    render() {
        let logoWidth = 60;
        let className = "contact-link ml-auto"
        let logoClassName = "col-lg-2"
        let headerClassName = "header-style"
        if (this.state.scrolledDown == true) {
            logoWidth = 30;
            className = "ml-auto d_n";
            headerClassName += " h_42";
        }
        return (
            <nav className={headerClassName} >
                <div className="container">
                    <div className="row" >
                        <div className={logoClassName} Style="top:7px">
                            <Link className="brand-style" to="/">
                                <img src={logo} width={logoWidth} Style="-webkit-transition: all 0.6s ease; " />
                            </Link>
                        </div>
                        <div className="col-lg-10">
                            <div className="row">

                                <div className={className} Style=" -webkit-transition: ease 0.6s">
                                    <span><svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" fill="currentColor" class="bi bi-telephone-fill" viewBox="0 0 16 16">
                                        <path fill-rule="evenodd" d="M1.885.511a1.745 1.745 0 0 1 2.61.163L6.29 2.98c.329.423.445.974.315 1.494l-.547 2.19a.678.678 0 0 0 .178.643l2.457 2.457a.678.678 0 0 0 .644.178l2.189-.547a1.745 1.745 0 0 1 1.494.315l2.306 1.794c.829.645.905 1.87.163 2.611l-1.034 1.034c-.74.74-1.846 1.065-2.877.702a18.634 18.634 0 0 1-7.01-4.42 18.634 18.634 0 0 1-4.42-7.009c-.362-1.03-.037-2.137.703-2.877L1.885.511z" />
                                    </svg> &nbsp;</span>
                                    Phone: +91 6290515957 &nbsp;
                                    <span ><svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" fill="currentColor" class="bi bi-envelope-fill" viewBox="0 0 16 16">
                                        <path d="M.05 3.555A2 2 0 0 1 2 2h12a2 2 0 0 1 1.95 1.555L8 8.414.05 3.555zM0 4.697v7.104l5.803-3.558L0 4.697zM6.761 8.83l-6.57 4.027A2 2 0 0 0 2 14h12a2 2 0 0 0 1.808-1.144l-6.57-4.027L8 9.586l-1.239-.757zm3.436-.586L16 11.801V4.697l-5.803 3.546z" />
                                    </svg>&nbsp;</span>
                                    Email: info@vbest.com
                                </div>
                            </div>
                            <div className="row">
                                <ul className="list-link ml-auto" Style="margin-top:-8px">
                                    <li className="list-link-item"> <Link to="/">Home </Link></li>
                                    <li className="list-link-item"> <Link to="/contact">Product Gallery </Link></li>
                                    <li className="list-link-item"> <Link to="/contact">Package Listing </Link></li>
                                    <li className="list-link-item"> <Link to="/contact">Contact Us </Link></li>
                                    <li className="list-link-item"> <Link to="/contact">About Us </Link></li>
                                </ul>
                            </div>
                        </div>
                    </div>


                </div>
            </nav>
        );
    }
}

export default Header;